// –£—Ç–∏–ª–∏—Ç–∞ –¥–ª—è –æ—Ç–ø—Ä–∞–≤–∫–∏ —Ñ–æ—Ä–º –≤ Telegram

export const sendToTelegram = async (formData, product = null) => {
  try {
    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –±–∞–∑–æ–≤—ã–π URL API (–ª–æ–∫–∞–ª—å–Ω–æ —à–ª—ë–º –Ω–∞ –ø–æ—Ä—Ç —Å–µ—Ä–≤–µ—Ä–∞)
    // –í—Å–µ–≥–¥–∞ —à–ª—ë–º –Ω–∞ –ø—Ä–æ–¥-—Å–µ—Ä–≤–µ—Ä (Render), —á—Ç–æ–±—ã –∏–∑–±–µ–∂–∞—Ç—å 404 –æ—Ç CRA dev-—Å–µ—Ä–≤–µ—Ä–∞
    const API_BASE = 'https://electro-1-vjdu.onrender.com';

    // –û—Ç–ø—Ä–∞–≤–ª—è–µ–º –¥–∞–Ω–Ω—ã–µ –Ω–∞ –Ω–∞—à API endpoint
    const response = await fetch(`${API_BASE}/api/send-telegram`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        name: formData.name,
        phone: formData.phone,
        message: formData.message,
        product: product
      })
    });
    
    const result = await response.json();
    
    if (result.success) {
      return { success: true, message: '–ó–∞—è–≤–∫–∞ —É—Å–ø–µ—à–Ω–æ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–∞!' };
    } else {
      console.error('–û—à–∏–±–∫–∞ API:', result.error);
      return { success: false, message: result.error || '–û—à–∏–±–∫–∞ –æ—Ç–ø—Ä–∞–≤–∫–∏ –∑–∞—è–≤–∫–∏' };
    }
    
  } catch (error) {
    console.error('–û—à–∏–±–∫–∞ –æ—Ç–ø—Ä–∞–≤–∫–∏ –≤ Telegram:', error);
    return { success: false, message: '–û—à–∏–±–∫–∞ –æ—Ç–ø—Ä–∞–≤–∫–∏ –∑–∞—è–≤–∫–∏' };
  }
};

// –§—É–Ω–∫—Ü–∏—è –¥–ª—è –≤–∞–ª–∏–¥–∞—Ü–∏–∏ —Ñ–æ—Ä–º—ã
export const validateForm = (formData) => {
  const errors = {};
  
  if (!formData.name.trim()) {
    errors.name = '–ò–º—è –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è';
  }
  
  if (!formData.phone.trim()) {
    errors.phone = '–¢–µ–ª–µ—Ñ–æ–Ω –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è';
  } else if (!/^[+]?[0-9\s\-()]{10,}$/.test(formData.phone)) {
    errors.phone = '–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –Ω–æ–º–µ—Ä —Ç–µ–ª–µ—Ñ–æ–Ω–∞';
  }
  
  return {
    isValid: Object.keys(errors).length === 0,
    errors
  };
}; 